import * as React from 'react';

import { getCN, truncateStr } from '@miq/utiljs';
import { Button, ConfirmButton, Icons } from '@miq/componentjs';

import { StaffSection } from '../components';

import { THit } from './types';
import { Hit, THitInstance } from './models';
import { hitIsBot, hitIsFb, hitIsIg, hitsByIp } from './utils';

export const HitSummarySection = (props: { title?: string }) => {
  // const { res } = useRequest(() => Hit({}).getSummary());
  const { title = 'Summary' } = props;

  return (
    <StaffSection {...{ title }} requiredPerms={['analytics.view_hit']}>
      Here goes a summary
    </StaffSection>
  );
};

const HitListItem = ({ item }: { item: THit }) => {
  const [isDeleted, setDeleted] = React.useState(false);

  if (isDeleted) return null;

  const instance = Hit(item);
  const { url, referrer, response_status, created } = instance;

  return (
    <div className="hit-list-item d-grid grid-md-5 text-sm py-1">
      <div className="span-md-2">
        <a href={url} target="_blank" title={url}>
          {item.path}
        </a>

        <div className="text-muted" title="session key">
          {item.session}
        </div>

        <span className={getCN([response_status! > 299 ? 'text-danger' : 'text-success'])} title="response status">
          {response_status}
        </span>

        <span title="Date created">{` / ${new Date(created!).formatDateToStr()} | ${new Date(
          created!
        ).formatTime()}`}</span>
        {item.debug && <span>{` / Debug`}</span>}
      </div>

      <div className="" title={`Referrer: ${referrer}`}>
        {truncateStr(referrer || '')}
      </div>

      <div className="" title={`User agent: ${item.user_agent}`}>
        {truncateStr(item?.user_agent || '')}
      </div>

      <div className="">
        <HitDeleteButton instance={instance} onDelete={() => setDeleted(true)} />
      </div>
    </div>
  );
};

export const HitList = ({ items }: { items: THit[] }) => {
  return (
    <div className="hit-list">
      {Object.entries(hitsByIp(items)).map((item) => {
        let [ip, h] = item;
        const hits = h.reverse();
        const isFb = Boolean(hits.filter((item: THit) => hitIsFb(item)).length);
        const isIg = hits.find((item: THit) => hitIsIg(item));
        const isBot = hits.find((item: THit) => hitIsBot(item));
        let user = hits[0]?.source_id?.match(/-(\w+)___/gm)?.[0]?.slice(1, -3);

        return (
          <StaffSection
            border
            title={
              <div className="d-flex align-items-center">
                <span className="d-flex">
                  {isBot && <Icons.Robot className="me-1 text-muted" width={20} height={20} />}
                  {isIg && <Icons.Instagram className="me-1" />}
                  {isFb && <Icons.Facebook className="text-primary me-1" />}
                </span>
                <span className="text-sm fw-bold">{user || ip}</span>
              </div>
            }
            actions={
              <div className="">
                <HitListDeleteButton slugs={hits.map((i: THit) => i?.slug)} onDelete={() => {}} />
              </div>
            }
            className="mb-1"
            bodyCN="p-0"
            key={ip}
          >
            {hits
              .sort((a: THit, b: THit) => (new Date(a.created!) > new Date(b.created!) ? -1 : 1))
              .map((i: THit) => (
                <HitListItem item={i} key={i.slug} />
              ))}
          </StaffSection>
        );
      })}
    </div>
  );
};

export const HitDeleteButton = ({
  instance,
  ...props
}: {
  label?: string;
  instance: THitInstance;
  onDelete?: (payload?: any) => void;
}) => {
  const { label = '', onDelete } = props;

  const handleDelete = () => {
    instance
      .destroy()
      .then((res) => {
        onDelete?.(res.data);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <ConfirmButton
      renderHeader={() => (
        <div className="d-flex align-items-center">
          <Icons.ExclamationTriangleFill />
          <span className="ms-1">Please confirm</span>
        </div>
      )}
      renderFooter={({ setOpen }) => (
        <div className="d-flex justify-content-between align-items-center">
          <Button onClick={() => setOpen(false)}>Annuler</Button>

          <Button onClick={handleDelete} className="btn-danger">
            Supprimer
          </Button>
        </div>
      )}
      render={() => (
        <div className="p-3">
          <div>You're about to delete this hit: {`${instance.slug}`}</div>
          <div>You cannot undo this action</div>
        </div>
      )}
      className={getCN(['btn-danger-3'])}
    >
      <Icons.Trash />
      {label && <span className="label">{label}</span>}
    </ConfirmButton>
  );
};

export const HitListDeleteButton = ({
  slugs,
  ...props
}: {
  label?: string;
  slugs: string[];
  onDelete?: (payload?: any) => void;
}) => {
  const { label = '', onDelete } = props;

  const handleDelete = (setOpen: any) => {
    Hit({})
      .deleteSlugs(slugs)
      .then((res) => {
        onDelete?.(res.data);
        setOpen(false);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <ConfirmButton
      renderHeader={() => (
        <div className="d-flex align-items-center">
          <Icons.ExclamationTriangleFill />
          <span className="ms-1">Please confirm</span>
        </div>
      )}
      renderFooter={({ setOpen }) => (
        <div className="d-flex justify-content-between align-items-center">
          <Button onClick={() => setOpen(false)}>Annuler</Button>

          <Button onClick={() => handleDelete(setOpen)} className="btn-danger">
            Supprimer
          </Button>
        </div>
      )}
      render={() => (
        <div className="p-3">
          <div>You're about to delete {`${slugs.length} hits.`}</div>
          <div>You cannot undo this action</div>
        </div>
      )}
      className={getCN(['btn-danger-3'])}
      title="Delete group"
    >
      <Icons.Trash />
      {label && <span className="label">{label}</span>}
    </ConfirmButton>
  );
};
