import { Response } from '@miq/utiljs';
import { StaffSection } from '../components';
import StaffView from '../Views';
import { HitList, HitSummarySection } from './components';
import { useHitListRequest } from './utils';

export const IndexView = () => {
  const { res } = useHitListRequest();

  const r = new Response(res);

  if (!r.isSuccess) return null;

  return (
    <StaffView title="Analytics" back="/staff/">
      <HitSummarySection />

      <StaffSection title="Last 100 hits">
        <HitList items={r.items} />
      </StaffSection>
    </StaffView>
  );
};
